
import {   fetchNewsList,
           fetchJobsList,
           fetchAskList,
           fetchUserInfo
        } from '../API/index.js';

export default {
  FETCH_NEWS(context){
    fetchNewsList()
      .then(res => {
        console.log(res);
        context.commit('SET_NEWS', res.data);
      })
      .catch(err => console.log(err)
      )},

  FETCH_ASK(context){
    fetchAskList()
    .then(res => {
      context.commit('SET_ASK',res.data);
    })
    .catch(err => console.log(err)
    )},
    FETCH_JOBS({commit}) {
      fetchJobsList()
      .then( ({data}) => context.commit('SET_JOBS', data))
      .catch(err => console.log(err));
     },
     FETCH_USER( {commit }) {
      fetchUserInfo()
      .then()
      .catch();
     }
    }